	module.exports = {"data":{"post":{"html":"<p>We decided to spend some money on ourselves rather than the business for a change. When we built our house back in 1996, when we got to designing the bathroom we had run out of both money and imagination. We ended with boring hospital-white. We* tore everything out, replaced and rearranged the fixtures, threw out the tub, built a new wall, replaced the door and window, and installed a walk-in spa-like shower. After two and half weeks without a bath, it's done enough that we can use it. We're missing a few details yet, like towel hooks, toilet paper dispenser, mirrors, and cabinets.</p>\n<p>*Of course, \"we did\" actually means \"we hired professionals to do\", but we did build the Ikea Godmorgen sink cabinet ourselves. </p>\n<p><strong>Update:</strong> I've now added panorama photos und put them all in a Flickr album.</p>\n{::nomarkdown}<div style='position: relative; padding-bottom: 76%; height: 0; overflow: hidden;'><iframe id='iframe' src='http://flickrit.com/slideshowholder.php?height=75&size=big&setId=72157644926436595&thumbnails=0&transition=0&layoutType=responsive&sort=0' scrolling='no' frameborder='0'style='width:100%; height:100%; position: absolute; top:0; left:0;' > </iframe></div>{:/nomarkdown}","fields":{"title":"Godmorgen","date":"30 May 2014","url":"https://papascott.de/2014-05-30-godmorgen/"}}},"pathContext":{"slug":"/2014-05-30-godmorgen/","prev":{"fields":{"title":"Phone Tricks 2014","date":"31 Mar 2014","slug":"/2014-03-31-phone-tricks-2014/"}},"next":{"fields":{"title":"Absence","date":"04 Oct 2014","slug":"/2014-10-04-absence/"}}}}

